<template>
  <!--购物车-->
  <div id="card">
    <home-header :title="'购物车'"></home-header>
    <main class="card-main" v-if="infoList && infoList.length != 0">
      <div class="card-main-totel">
        共{{totalCount}}件商品，总价(不含运费)：<span>￥{{infoList[0].productTotalPrice}}元</span>
      </div>
      <div class="card-main-list">
        <div class="card-main-li" v-for="(item,index) in infoList" :key="index">
          <div class="card-li-top">
            <div class="card-li-image" @click="$router.push({name:'proDetail',query:{proId:item.productId}})">
              <img :src="imgHost+item.productMainImage" alt="">
            </div>
            <div class="card-li-content">
              <div class="card-li-title" @click="$router.push({name:'proDetail',query:{proId:item.productId}})">
                {{item.productName}}
              </div>
              <!--需要修改-->
              <div class="card-li-subtitle">
                <div class="card-subtitle-name">{{ item.productText | filterParse}}</div>
              </div>
              <div class="card-li-money">
                ￥{{item.productPrice}}元
              </div>
            </div>
          </div>
          <div class="card-li-bottom">
            <div class="sel-minus-add">
              <div class="sel-minus" @click="minusCount(index)">
                -
              </div>
              <div class="sel-count">
                {{item.quantity}}
              </div>
              <div class="sel-add" @click="addCount(index)">
                +
              </div>
            </div>
            <div class="card-li-del iconfont icon-shanchu" @click="postDel(item.id)">

            </div>
          </div>
        </div>
      </div>
      <div class="card-main-settle">
        <span @click="hrefName('orderCart')">立即结算</span>
      </div>
    </main>
    <main class="card-main card-main-else" v-else>
      <p>没有商品</p>
      <div class="submit-button">
        <submit-btn :min="true" :title="'去添加购物车'" @click.native="hrefName('classify')"></submit-btn>
      </div>
    </main>
  </div>
</template>

<script>
  import homeHeader from '../../components/header/header.vue'
  import submitBtn from '../../components/button/submitBtn.vue'
  import {filterParse} from '../../mixins/filter'

  export default {
    name: 'carrousel',
    data() {
      return {
        totalCount: '1',
        totalMoney: '100',
        infoList: null,
        attribute: null
      }
    },
    computed: {},
    components: {
      homeHeader,
      submitBtn
    },
    filters: {
      filterParse
    },
    methods: {
      minusCount(index) {
        if (this.infoList[index].quantity > 1) {
          this.infoList[index].quantity--;
        }
      },
      addCount(index) {
        if (this.infoList[index].quantity < 100) {
          this.infoList[index].quantity++;
        }
      },
      postDel() {

      },
      submitBtn() {
        let that = this;
        if (that.agreementActive) {
          that.$router.push({path: '/order'})
        }
      },
      getList() {
        let that = this;
        let memberId = window.sessionStorage.getItem('tempMemberId') ? window.sessionStorage.getItem('tempMemberId') : window.localStorage.getItem('memberId');
        that.ajax({
          url: '/cart/list?memberId=' + memberId,
          success: function (res) {
            that.infoList = res.data.cartProductDtoList;
            console.log(JSON.parse(that.infoList[0].productText))
            let attribute = '';
            for (var i = 0, l = that.infoList.length; i < l; i++) {
              for (var key in that.infoList[i]) {
                attribute += key + ':' + that.infoList[i][key] + '';
              }
            }
            that.attribute = attribute;
          }
        })
      }
    },
    mounted() {

    },
    created() {
      this.getList()
    },
    updated() {
      if (this.infoList) {
        var total = 0;
        var count = 0;
        for (var i = 0, l = this.infoList.length; i < l; i++) {
          total += this.infoList[i].productPrice * this.infoList[i].quantity;
          count += this.infoList[i].quantity;
        }
        try{
          this.infoList[0].productTotalPrice = total;
          this.totalCount = count;
        }catch(err) {

        }
      }

    },
  }
</script>

<style lang="scss" scoped>
  @import "../../style/minxin";

  #card {
    float: left;
    width: 100%;
  }

  .card-main {
    padding: $top 0 $bottom;
  }

  .card-main-totel {
    padding: 0 10px;
    @include sh($min, 2em);
    color: $gray;
    span {
      color: $red;
    }
  }

  .card-main-li {
    @include fj;
    flex-direction: column;
    background: $f;
    border-bottom: 1px solid $border;
    padding: 8px 10px;
    .card-li-top {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      padding-bottom: 10px;
      .card-li-image {
        @include wh(6rem, 6rem);
        img {
          @include wh(6rem, 6rem);
        }
      }
      .card-li-content {
        padding-left: 10px;
        @include fj;
        flex-direction: column;
        .card-li-title {
          @include el(2);
          @include sh($med, 1.5em);
          flex: 1;
        }
        .card-li-subtitle {
          display: flex;
          flex-direction: row;
          justify-content: flex-start;
          @include sc(0.4rem, $grey);
        }
        .card-li-money {
          color: $red;
        }
      }
    }
    .card-li-bottom {
      @include fj;
      //加减器
      .sel-minus-add {
        display: flex;
        flex-direction: row;
        border: 1px solid $border;
        @include sh($med, 2em);
        text-align: center;
        .sel-minus {
          width: 2.2rem;

        }
        .sel-count {
          border-right: 1px solid $border;
          border-left: 1px solid $border;
          min-width: 3.4rem;
        }
        .sel-add {
          width: 2.2rem;
        }
      }
      .card-li-del {
        @include sc($max, $grey);
        padding-top: 0.3rem;
      }
    }
  }

  .card-main-settle {
    margin: 10px;
    span {
      display: block;
      @include wh(100%, 2.6em);
      @include sc($med, $f);
      line-height: 2.6em;
      text-align: center;
      background: $red;
      border-radius: 3px;
      letter-spacing: 1px;
    }
  }

  .card-main-else {
    p {
      padding: 10rem;
      text-align: center;
    }
    .submit-button {
      padding: 3rem;
    }
  }

</style>



